.picture {
  border-radius: 50%;
  margin: 0 auto;
}
a {
  color: #333333;
  text-decoration: none;
}
a:visited {
  color: #4e4e4e;
}
a:hover {
  color: #4e4e4e;
  text-decoration: underline;
}

.header {
  text-align:center;
  font-size: 2em;
  margin-top:1em;
  margin-left:5%;
  margin-right:5%;
}
h1 {
  font-size:3em;
  margin-bottom:0;
}
.profile {
  font-weight:bold;
  font-family: Montserrat;
  font-size: 1em;
  margin:0;
  margin-left: 10px;
}

.contact-header {
  font-weight:bold;
}
.label {
  margin-top: 0;
  margin-bottom: 1em;
}
.fa {
  font-size:2em;
}
.left {
  font-size: 1em;
}
.main {
  margin: 0 auto;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 50px;
}
h2 {
  font-size: 1.5rem;
  margin-bottom: 0;
}
h3 {
  font-size:2em;
  margin-bottom:0;
}
.company-name {
  margin-left: 5px;
  color: #333333;
  text-decoration: none;
  cursor: pointer;
}
.company-name:visited {
  color: #4e4e4e;
}
.company-name:hover {
  color: #4e4e4e;
  text-decoration: underline;
}

h4 {
  color: #4e4e4e;
  font-size:1.5em;
  margin-top:0;
  margin-bottom: 0;
}
.skills {
  margin-bottom: 10px;
  margin-top: 10px;
}
p {
  font-family: 'Open Sans';
  /* font-size: 2em; */
}
li {
  font-family: 'Open Sans';
  font-size: 1.7em;
}
.keywords {
  background-color: #333333;
  color:#fff;
  padding: 6px;
  font-size: 1.5em;
  margin: 0px 4px 5px;
  display:inline-block;
}
blockquote {
  margin: 2em 10px;
    padding: 1em 10px;
  border-left: 10px solid #edf0f5;
  font-family: 'Open Sans';
  font-size: 1.5em;
}
blockquote .name {
  font-weight:bold;
  font-family: "Montserrat";
}
.responsive-show {
  display:none;
}
.award {
  margin-bottom: 25px;
}
.link {
  font-size: 2rem;
}
.job {
  margin-top: 0;
}

#profiles *{
  display: inline-block;
}
@media (max-width: 1000px) {
    .header {
      font-size: 1.66em;
    }
    h2 {
        font-size: 3em;
    }
}
@media (max-width: 500px) {
  p {
      font-size:1.2em;
  }
  li {
      font-size:1.2em;
  }
  blockquote {
      font-size:1.2em;
  }
  h2 {
      font-size:2.3em;
  }
  .responsive-hidden {
      display:none;
      visibility: hidden;
  }
  .responsive-show {
      display:inline;
      visibility: visible;
  }
}